import * as React from "react";
import TableContainer from "@material-ui/core/TableContainer/TableContainer";
import Paper from "@material-ui/core/Paper/Paper";
import Table from "@material-ui/core/Table/Table";
import TableHead from "@material-ui/core/TableHead/TableHead";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody/TableBody";
import DeleteOutlinedIcon from "@material-ui/core/SvgIcon/SvgIcon";
import ApiCaller from "../../api/ApiCaller";
import UrlConst from "../../const/UrlConst";
import SockJsClient from "react-stomp";

class DeviceMachine extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
    }

    componentDidMount() {
        ApiCaller.call(UrlConst.GET_ALL_DEVICES, null, (data) => {
            this.setState({data: data.deviceDTOList});
        });
    }

    refresh() {
        ApiCaller.call(UrlConst.GET_ALL_DEVICES, null, (data) => {
            this.setState({data: data.deviceDTOList});
        });
    }

    deleteDevice(serialNumber) {
        ApiCaller.call(UrlConst.REMOVE_DEVICE + '/' + serialNumber, null, (data) => {
            this.refresh();
        });
    }

    render() {
        const rows = this.getRows();

        return (<div>
            <SockJsClient url='http://localhost:8080/ws' topics={['/topics/device/update']}
                          onMessage={(msg) => {
                              this.deviceUpdate(msg);
                          }}
                          ref={(client) => {
                              this.clientRef = client
                          }}/>
            <TableContainer component={Paper}>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Serial number</TableCell>
                            <TableCell align="right">Popisek</TableCell>
                            <TableCell align="right">State</TableCell>
                            <TableCell align="right">Vytvořen</TableCell>
                            <TableCell align="right">Akce</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows}
                    </TableBody>
                </Table>
            </TableContainer></div>);
    }

    getRows() {
        let data = this.state.data;
        return data.map((value, key) => {
            let date = new Date(value.created);
            return (
                <TableRow key={value.serialNumber}>
                    <TableCell component="th" scope="row">
                        {value.serialNumber}
                    </TableCell>
                    <TableCell align="right">{value.deviceDescription}</TableCell>
                    <TableCell align="right">{value.deviceStateEntity}</TableCell>
                    <TableCell align="right">{
                        date.getDate() + "-" + (date.getMonth() + 1) + "-" + date.getFullYear() + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds()
                    }
                    </TableCell>
                    <TableCell align={"right"}><DeleteOutlinedIcon onClick={
                        (e) => {
                            this.deleteDevice(value.serialNumber);
                        }
                    }/>
                    </TableCell>
                </TableRow>
            );
        });

    }

    deviceUpdate(msg) {
        this.setState({data: msg.deviceDTOList});
    }
}

export default DeviceMachine;