import Card from "@material-ui/core/Card/Card";
import CardHeader from "@material-ui/core/CardHeader/CardHeader";
import CardIcon from "../../components/Card/CardIcon";
import Store from "@material-ui/core/SvgIcon/SvgIcon";
import CardBody from "../../components/Card/CardBody";
import {Chart} from "react-charts";
import Paper from "@material-ui/core/Paper/Paper";
import * as React from "react";
import Typography from "@material-ui/core/Typography/Typography";

class TensorGraph extends React.Component {
    render() {
        const useData = this.props.data;
        const dataGraph = [
            {
                label: 'Series 1',
                data: useData.map((value, key) => {
                    return [key, value[this.props.dataKey]];
                })
            }
        ];
        const axes = [
            { primary: true, type: 'linear', position: 'bottom' },
            { type: 'linear', position: 'left' }
        ];
        return(
            <Card>
                <CardHeader color="success" stats icon>
                </CardHeader>
                <CardBody>
                    <Typography variant={"h4"}>{this.props.title}</Typography>
                    <div
                        style={{
                            width: 'auto',
                            height: this.props.width,
                            padding: '5px'
                        }}
                    >
                        <Chart data={dataGraph} axes={axes} />
                    </div>
                </CardBody>
            </Card>
        );
    }
}

export default TensorGraph;