import SockJsClient from "react-stomp";
import * as React from "react";
import TableContainer from "@material-ui/core/TableContainer/TableContainer";
import Paper from "@material-ui/core/Paper/Paper";
import Table from "@material-ui/core/Table/Table";
import TableHead from "@material-ui/core/TableHead/TableHead";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody/TableBody";


class OnlineTable extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        }
    }

    onNewMessage(msg) {
        this.setState({data: msg});
    }

    render() {
        let rows = this.prepareRows(this.state.data);
        return (
            <div>
                <SockJsClient url='http://localhost:8080/ws' topics={['/topics/online']}
                              onMessage={(msg) => {
                                  this.onNewMessage(msg);
                              }}
                              ref={(client) => {
                                  this.clientRef = client
                              }}/>


                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Seriál number</TableCell>
                                <TableCell align="right">Poslední záznam</TableCell>
                                <TableCell align="right">Stroj</TableCell>
                                <TableCell align="right">Max Rychlost</TableCell>
                                <TableCell align="right">Max vzdálenost</TableCell>
                                <TableCell align="right">Interval</TableCell>
                                <TableCell align="right">Online</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        );
    }

    prepareRows(data) {
       return data.map((value, key) => {
           let date = new Date(value.lastRecord);
            return (
                <TableRow key={key}>
                    <TableCell component="th" scope="row">{value.serialNumber}</TableCell>
                    <TableCell align="right">{

                        date.getDate() + "-" + (date.getMonth() + 1) + "-" + date.getFullYear() + " " + date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds()

                    }</TableCell>
                    <TableCell align="right">{value.machineName}</TableCell>
                    <TableCell align="right">{value.maxSpeed}</TableCell>
                    <TableCell align="right">{value.maxDistance}</TableCell>
                    <TableCell align="right">{value.interval}</TableCell>
                    <TableCell align="right">Ano</TableCell>
                </TableRow>
            );
        });
    }
}

export default OnlineTable;