import React from "react";
import Compass from "../../components/Compass/compass"
import CardHeader from "@material-ui/core/CardHeader/CardHeader";
import CardBody from "../../components/Card/CardBody";
import Typography from "@material-ui/core/Typography/Typography";
import Card from "@material-ui/core/Card/Card";

class CompasGraph extends React.Component {

    render() {
        let last = this.props.data[this.props.data.length - 1];
        let x = last.speedX;
        let y = last.speedY;
        let size = Math.sqrt(x * x + y * y);
        let cos = x / size;
        let angle = Math.acos(cos);
        return (
            <div>
               <div style={{ "width": "300px" ,"margin": "0 auto", "textAlign": "center"}}>
                <Compass direction={angle * 180 / Math.PI}/>
               </div>
                   <Card>
                    <CardHeader color="success" stats icon>
                    </CardHeader>
                    <CardBody>
                        <Typography variant={"h4"}>Aktuální směr</Typography>

                    </CardBody>
                </Card>

            </div>);

    }
}

export default CompasGraph;