import * as React from "react";
import Store from "@material-ui/core/SvgIcon/SvgIcon";
import Card from "@material-ui/core/Card/Card";
import CardHeader from "@material-ui/core/CardHeader/CardHeader";
import MenuItem from "@material-ui/core/MenuItem/MenuItem";
import Select from "@material-ui/core/Select/Select";
import FormControl from "@material-ui/core/FormControl/FormControl";
import SockJsClient from "react-stomp";
import CardBody from "../../components/Card/CardBody";
import CardIcon from "../../components/Card/CardIcon";

import {bugs, server, website} from "variables/general.js";

import {completedTasksChart, dailySalesChart, emailsSubscriptionChart} from "variables/charts.js";
import Grid from "@material-ui/core/Grid/Grid";
import Paper from "@material-ui/core/Paper/Paper";
import DataGraph from "./TensorGraph";
import LastDataCard from "./LastDataCard";
import CompasGraph from "./CompasGraph";
import Typography from "@material-ui/core/Typography/Typography";

import StreamDataHolder from "../../holder/StreamDataHolder";
import Icon from "@material-ui/core/Icon/Icon";
import OnlineTable from "./OnlineTable";

class GraphView extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: new Map(),
            vins: new Set(),
            selected: null
        }
    }

    componentDidMount() {
        if(StreamDataHolder.DATA !== undefined) {
            const vins = StreamDataHolder.VINS;
            const selected = StreamDataHolder.SELECTED;
            this.setState({
                data: StreamDataHolder.DATA,
                vins: vins,
                selected : selected
            });
        }
    }

    handleChange(event) {
        this.setState({selected: event.target.value});
    }

    onNewMessage(msg) {
        const vinCode = msg.vin;
        let data = this.state.data;
        if (data[vinCode] === undefined) {
            let arr = [msg];
            data[vinCode] = arr;
        } else {
            data[vinCode].push(msg);
        }
        const vins = this.state.vins;
        vins.add(msg.vin);
        this.setState({data: data, vins: vins});
        if (this.state.selected === null) {
            this.setState({selected: msg.vin});
        }
        StreamDataHolder.DATA = data;
        StreamDataHolder.VINS = vins;
        StreamDataHolder.SELECTED = this.state.selected;
    }

    render() {
        let values = this.prepareItems(this.state.vins);
        let graphs = this.prepareGraphs(this.state.data, this.state.selected);
        return (
            <div style={{flexGrow: 1}}>
                <SockJsClient url='http://localhost:8080/ws' topics={['/topics/newRecord']}
                              onMessage={(msg) => {
                                  this.onNewMessage(msg);
                              }}
                              ref={(client) => {
                                  this.clientRef = client
                              }}/>

                <Grid>
                    <Grid xs={12} sm={12} md={12}>
                        <Paper style={{"textAlign": "center"}}>
                            <Card>
                                <CardHeader color="success" stats icon>
                                    <CardIcon color="success">
                                        <Icon>event_seat</Icon>
                                    </CardIcon>
                                </CardHeader>
                                <CardBody>
                                    <Typography variant={"h4"}>Vyberte stroj</Typography>
                                    <FormControl>
                                        <Select
                                            labelId="demo-simple-select-label"
                                            id="demo-simple-select"
                                            onChange={this.handleChange.bind(this)}
                                            value={this.state.selected}
                                        >
                                            {values}
                                        </Select>
                                    </FormControl>
                                </CardBody>
                            </Card>
                        </Paper>
                    </Grid>
                    {graphs}
                </Grid>
            </div>
        );
    }

    prepareItems(vins) {
        let items = [];
        for (let item of vins.keys()) {
            items.push((<MenuItem key={item} value={item}>{item}</MenuItem>));
        }
        return items;
    }

    prepareGraphs(data, selected) {
        if (selected === null) {
            return;
        }


        return (
            <Grid>
                <Grid container style={{textAlign: "center"}}>
                    <Grid xs={6} sm={6} md={6}>
                        <Paper style={{"textAlign": "center", padding: "2px", margin: "2px"}}>
                            <LastDataCard data={this.state.data[this.state.selected]}/>
                        </Paper>
                    </Grid>
                    <Grid xs={6} sm={6} md={6}>
                        <Grid>
                            <Paper style={{"textAlign": "center", padding: "2px", margin: "2px"}}>
                                <DataGraph dataKey={"tensometer"} data={this.state.data[this.state.selected]} title={"Tensometr"} width={"270px"}/>
                            </Paper>
                        </Grid>
                        <Grid>
                            <Paper style={{"textAlign": "center", padding: "2px", margin: "2px"}}>
                                <DataGraph dataKey={"streesBattery"} data={this.state.data[this.state.selected]} title={"Strees Battery"} width={"270px"}/>
                            </Paper>
                        </Grid>
                    </Grid>

                </Grid>
                <Grid container style={{textAlign: "center"}}>
                    <Grid xs={4} sm={4} md={6}>
                        <Paper style={{"textAlign": "center", padding: "2px", margin: "2px"}}>
                            <CompasGraph data={this.state.data[this.state.selected]}/>
                        </Paper>
                    </Grid>
                    <Grid xs={4} sm={4} md={6}>
                        <Paper style={{"textAlign": "center", padding: "2px", margin: "2px"}}>
                            <DataGraph dataKey={"humidity"} data={this.state.data[this.state.selected]} title={"Humidity"} width={"270px"}/>
                        </Paper>
                    </Grid>
                </Grid>

            </Grid>
        );
    }
}

export default GraphView;