import Snackbar from "@material-ui/core/Snackbar/Snackbar";
import {AddAlert} from "@material-ui/icons";
import Card from "../Card/Card";
import CardBody from "../Card/CardBody";
import Grid from "@material-ui/core/Grid/Grid";
import TextField from "@material-ui/core/TextField/TextField";
import CardFooter from "../Card/CardFooter";
import Button from "../CustomButtons/Button";
import ApiCaller from "../../api/ApiCaller";
import UrlConst from "../../const/UrlConst";
import React from "react";

class CreateDeviceForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            serialNumber: "",
            description: "",
            message: null
        }
    }

    getMessage() {
        return (<div></div>);

        if (this.state.message !== null)
            return (<Snackbar
                place="bc"
                color="info"
                icon={AddAlert}
                message={this.state.message}
                open={true}
                onClick={() => {
                    this.setState({message: null})
                }}
            />);
        else {
            return (<div></div>);
        }
    }

    render() {
        const message = this.getMessage();
        return (
            <div>
                {message}
                <Card>
                    <CardBody>
                        <Grid container style={{textAlign: "center"}}>
                            <Grid xs={12} sm={12} md={3}>
                                <TextField id="standard-basic" label="Sériové číslo"
                                           value={this.state.serialNumber}
                                           onChange={this.onBlureSerialNumber.bind(this)}
                                />
                            </Grid>
                        </Grid>
                        <Grid>
                            <Grid style={{"marginTop": "10px", "marginBottom": "10px"}}/>
                        </Grid>
                        <Grid container style={{textAlign: "center"}}>
                            <Grid xs={12} sm={12} md={12}>
                                <TextField
                                    id="standard-multiline-flexible"
                                    label="Popis zarizeni"
                                    multiline
                                    onChange={this.onBlureDescription.bind(this)}
                                    value={this.state.deviceDescription}
                                    rowsMax={4}
                                    fullWidth
                                />
                            </Grid>
                        </Grid>
                    </CardBody>
                    <CardFooter>
                        <Button color="danger" onClick={this.sendData.bind(this)}>Vytvořit zažízení</Button>
                    </CardFooter>
                </Card>
            </div>
        );
    }

    sendData() {
        ApiCaller.call(UrlConst.CREATE_DEVICE,
            {
                serialNumber: this.state.serialNumber,
                deviceDescription: this.state.deviceDescription,
                deviceStateEntity: "NEW"
            }, () => {
                this.clearAll();
                this.setState({message: "Zařízení bylo založeno."});
            });

    }

    onBlureSerialNumber(event) {
        let value = event.target.value;
        this.setState({serialNumber: value});
    }

    onBlureDescription(event) {
        let value = event.target.value;
        this.setState({deviceDescription: value});
    }

    clearAll() {
        this.setState({
            serialNumber: "",
            deviceDescription: ""
        });
    }
}

export default CreateDeviceForm;