import Card from "@material-ui/core/Card/Card";
import CardHeader from "@material-ui/core/CardHeader/CardHeader";
import CardBody from "../../components/Card/CardBody";
import Typography from "@material-ui/core/Typography/Typography";
import * as React from "react";
import TableContainer from "@material-ui/core/TableContainer/TableContainer";
import Paper from "@material-ui/core/Paper/Paper";
import TableHead from "@material-ui/core/TableHead/TableHead";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody/TableBody";
import Table from "@material-ui/core/Table/Table";
import AnimatedNumber from 'react-animated-number';

class LastDataCard extends React.Component {

    render() {
        const data = this.props.data;
        let current = data[data.length - 1];
        let last = data[data.length - 2];
        return (<Card>
            <CardHeader color="success" stats icon>
            </CardHeader>
            <CardBody>
                <Typography variant={"h4"}>Poslední data</Typography>

                <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>Druh dat</TableCell>
                                <TableCell align="right">Minulé</TableCell>
                                <TableCell align="right">Aktuální</TableCell>
                                <TableCell align="right">Rozdíl</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.prepareRow(current, last,["tensometer", "Tensometer"])}
                            {this.prepareRow(current, last,["gpsX", "gpsX"])}
                            {this.prepareRow(current, last,["gpsY", "gpsY"])}
                            {this.prepareRow(current, last,["humidity", "humidity"])}
                            {this.prepareRow(current, last,["speedX", "speedX"])}
                            {this.prepareRow(current, last,["speedY", "speedY"])}
                            {this.prepareRow(current, last,["speedZ", "speedZ"])}
                            {this.prepareRow(current, last,["streesBattery", "streesBattery"])}
                            {this.prepareRow(current, last,["restOfFuel", "restOfFuel"])}
                        </TableBody>
                    </Table>
                </TableContainer>
            </CardBody>
        </Card>);
    }

    prepareRow(current, last, keyPair) {
        const key = keyPair[0];
        return (
            <TableRow key={key}>
                <TableCell component="th" scope="row">
                    {keyPair[1]}
                </TableCell>
                <TableCell align="right">{this.prepareLastNumberAnimation(last, key)}</TableCell>
                <TableCell align="right">{this.prepareNumberAnimation(current, key)}</TableCell>
                <TableCell align="right">{this.prepareNumberAnimation(current, key, last, key)}</TableCell>
            </TableRow>
        );
    }

    prepareLastNumberAnimation(last, lastKey) {
        if(last === undefined)
            return;

        let showValue = last[lastKey];
        return (<AnimatedNumber component="text" value={showValue}
                                style={{
                                    transition: '0.8s ease-out',
                                    fontSize: 25,
                                    transitionProperty:
                                        'background-color, color, opacity'
                                }}
                                stepPrecision={1}

                                duration={1000}/>);
    }

    prepareNumberAnimation(current, currentKey, last, lastKey) {
        if (current === undefined || currentKey === undefined || current[currentKey] === undefined) {
            return;
        }
        let currentValue = current[currentKey];
        let lastValue = 0;
        if (last !== undefined && lastKey !== undefined && last[lastKey] !== undefined) {
            lastValue = last[lastKey];
        }

        let showValue = currentValue - lastValue;
        return (<AnimatedNumber component="text" value={showValue}
                                style={{
                                    transition: '0.8s ease-out',
                                    fontSize: 25,
                                    transitionProperty:
                                        'background-color, color, opacity'
                                }}
                                stepPrecision={1}

                                duration={1000}/>);
    }
}

export default LastDataCard;